<template>
    <div class="ol-checkbox-group">
        <slot></slot>
    </div>
</template>
<script>
import {catIn, MIXIN} from '../utils.js'
export default {
    name: 'olCheckBoxGroup',
    componentName: 'olCheckBoxGroup',
    mixins: [MIXIN],
    data() {
        return {
            checkDatas: []
        }
    },
    props: {
    },
    components: {
    },
    methods: {
        addDatas(obj, key) {
            let isSave = true;
            this.checkDatas.forEach((item, index) => {
                if (item[key] !== undefined) { //  
                    this.checkDatas.splice(index, 1);
                    isSave = false;   
                }
            })
            if (isSave)  {
                this.checkDatas.push(obj);
            }
            this.$emit('changing', obj, this); // 
            this.dispath('olForm', 'changeOlFromValue', [this, this.checkDatas]); //
        }
    }
}
</script>


